import React from "react"
import {Timeline} from 'react-twitter-widgets'

export default ({ data }) => {
    return (
        <Timeline
            dataSource={{
                sourceType: 'profile',
                screenName: 'Nick_Rance'
            }}
            options={{
                username: 'Nick_Rance',
                height: '500',
                width: '300',
            }}
            onLoad={() => console.log('Timeline is loaded!')}
        />
    )
}



